.filterDebtors{
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .filterName{
        margin: 15px 0;
        padding: 5px 7px 5px 23px;
        background: #c2c9eb;
        border-radius: 3px;
        position: relative;
        margin-right: 15px;
        cursor: pointer;
        &::before{
            content: '';
            position: absolute;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: #fff;
            left: 7px;
            top: 0;
            bottom: 0;
            margin: auto;
        }

        &.checked{
            background-color: #3c39db;
        }

        span{
            font-family: Jost;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 17px;
            text-transform: capitalize;
            color: #FFFFFF;
        }
    }
}